<template lang="">

    <!-- 헤더 -->
    <div class="w-full border-b h-20">
      <div class="max-w-7xl mx-auto h-full flex items-center">
        <!-- 로고 -->
        <div class="w-64 md:ml-8 absolute lg:static left-2/4 -translate-x-2/4 lg:translate-x-0 flex items-center">
          <img :src="require(`@/assets/images/logo_img.png`)" alt="로고" class="ml-1 w-full">
          <img :src="require(`@/assets/images/logo.png`)" alt="로고" class="ml-1 w-full">
        </div>
        <!-- 로고 -->
        <div class="ml-20">
          <ul class="hidden lg:flex font-bold text-lg">  <!-- mouseover 됐을때 SubMenuOpen 가 true, mouseout 됐을때 SubMenuOpen 가 false 가 되도록 한다. -->
            <li class="shrink-0 xl:px-8 lg:px-5 h-20 leading-[80px] transition-all relative after:absolute after:transition-all after:w-full after:h-0 after:bg-[#37a2d7] after:left-0 after:bottom-5 hover:after:h-1" v-for="(e, index) in NavList" :key="e" :class="NavSelectIndex == index && 'after:h-1 text-[#37a2d7]'" @mouseout="NavSelectIndex = null" @mouseover="NavSelectIndex = index; SubMenu(index)"><a href="#">{{e}}</a>
              <ul class="absolute z-10 flex flex-wrap bg-white rounded-br-2xl rounded-bl-2xl -ml-5 transition-all duration-300 h-0 overflow-hidden" :style="NavSelectIndex === index && isSubMenu">
                <template v-for="el in SubList[index-1]" :key="el">
                  <li v-if="index !==0" class="text-xs shrink-0 basis-full text-center pt-5 last-of-type:pb-5">
                    <a href="#" @click.prevent class="text-black hover:text-[#37a2d7]">{{ el }}</a>
                  </li>
                </template>
              </ul>
            </li> <!-- NavSelectIndex 값이랑 index 값이 같을때는 after 밑줄이 계속 나오도록 해준다. 즉 마우스가 서브메뉴에 있을때 밑줄이 유지되도록 한다. 그리고 마우스가 빠졌을때는 NavSelectIndex 값이 null 값으로 해줘야 네비게이션 메뉴에 밑줄이 사라진다. -->
          </ul>
        </div>
        <div class="lg:ml-20 w-6 h-6 hidden sm:block">
          <img :src="require(`@/assets/images/icon_lan.png`)" alt="언어변경" class="w-full">
        </div>
        <div class="w-6 h-6 -ml-10 sm:hidden">
          <img :src="require(`@/assets/images/icon_search.png`)" alt="검색" class="w-full">
        </div>
      </div>

    </div>
    <!-- 헤더 -->
    <!-- 햄버거 -->
    <div class="absolute right-4 md:right-8 top-6 duration-1000 transition-all z-50 cursor-pointer lg:hidden" @click="isOpen === true ? isOpen = false : isOpen = true" :class="isOpen && 'on'">
      <div v-for="e in 3" :key="e" class="w-[30px] h-0.5 bg-black rounded m-1.5 transition-all duration-1000"></div>
    </div>
    <div class="w-80 h-full fixed bg-gray-50 z-40 p-12 -right-80 box-border transition-all duration-500 top-0 lg:hidden overflow-y-scroll" :class="isOpen && '!right-0'">
      <ul class="mt-12">
        <li class="py-5 border-b" v-for="(e, index) in NavList" :key="e"><a href="#" class="font-bold" @click="NavSelectIndex = index; SubMenu(index)">{{ e }}</a>
          <template v-for="(e, i) in SubList" :key="e">
            <ul v-if="index === i + 1" class="pt-5 flex flex-wrap justify-between sub_list h-0 overflow-hidden transition-all duration-300" :style="NavSelectIndex === index && 'height: 116px'">
              <li v-for="el in e" :key="el" class="basis-2/4 pt-2 text-xs hover:text-[#37a2d7]">
                <a href="#">{{ el }}</a>
              </li>
            </ul>
          </template>
        </li>
      </ul>
    </div>
    <!-- 햄버거 --> <!--  :class="isOpen ? 'right-0' : '-right-80'" --> <!-- 3항 연산자를 써도 되고 :class="isOpen && '!right-0'" 를 써도 된다. tailwindcss 에서는 클래스 바인딩에 right-1 이렇게 값을 주면 먹히는데 right-0을 해버리면 0은 값이 없어서 안 먹힌다. 그래서 !right-0 으로 important 를 줘야한다. -->

</template>
<script>
import NavData from '../assets/Data.json'
export default {
  name: "NavPage",
  data() {
    return {
      NavList : NavData.Nav,
      SubList : NavData.SubMenu,
      SubMenuOpen: false,
      NavSelectIndex: null,
      isOpen: false,
      isSubMenu : '',
    }
  },
  methods: {
    SubMenu(e){
      if(e !== 0){
        const list = document.querySelectorAll(".sub_list")[e-1];
        const length = list.querySelectorAll("li").length;
        console.log(length);
        this.isSubMenu = `height:${length*52}px` // 여기서 세로 한칸 높이가 52px로 정해줘서 length를 써서 갯수만큼 높이를 곱해준다. script 에서 쓰면 tailwindcss 에서 `h-[${length*52}px]` 가 script를 먼저 읽기때문에 안먹힌다 그래서 위에서는 class에 tailwindcss 에 넣지않고 :style에 높이 값을 지정해준다. 넣는다. 원래 tailwindcss에 등록된 h-32나 h-96 의 값이 아닌 h-[500px]의 경우 template에서 class 나 :class 안에 적어도 한 번은 적어줘야 등록이 되어 컴파일 됐을때 쓸 수 있다.

        if(list.style.height === '0px'){
          list.style.height = '116px';
        } else{
          list.style.height = '0px';
        }
      }
    }
  },
}

</script>
<style>
.on div:nth-child(1){
  transform: rotate(45deg) translateY(12px);
}
.on div:nth-child(2){
  opacity: 0;
  transform: translateX(-30px) rotate(720deg);
}
.on div:nth-child(3){
  transform: rotate(-45deg) translateY(-12px);
}
</style>